import BbwzBaseView from "../BbwzBaseView";
import BbwzPokerTool from "../../tool/BbwzPokerTool";
import BbwzPathHelper from "../../tool/BbwzPathHelper";
import BbwzConstDefine from "../../data/BbwzConstDefine";

export default class BbwzPokerTypeView extends BbwzBaseView{
    public setPokerTypeStyle(nType: number, nMult: number){

    }
}

export class BbwzZjhPokerTypeView extends BbwzPokerTypeView{
    private typeSp: cc.Sprite;
    private multSp: cc.Sprite;
    private highTypeSk: sp.Skeleton;

    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.typeSp = this.getComponent("layout/typeSp", cc.Sprite);
        this.multSp = this.getComponent("layout/multSp", cc.Sprite);
        this.highTypeSk = this.getComponent("highSk", sp.Skeleton);
    }

    public setPokerTypeStyle(nType: number, nMult: number){
        this.typeSp.node.active = true;
        let atlasPath = BbwzPathHelper.gameTexturePath + "atlas/poker/atlas_poker";
        Global.ResourceManager.loadBundleAutoAtlas(BbwzConstDefine.GAME_ID,this.typeSp, atlasPath, BbwzPokerTool.zjhPokerTypeSf[nType], null, true);
        Global.ResourceManager.loadBundleAutoAtlas(BbwzConstDefine.GAME_ID,this.multSp, atlasPath, BbwzPokerTool.multiSfRes[nMult], null, true);

        let sort = BbwzPokerTool.sortZjhPokerType(nType);
        let isSpecial = sort == 2 || sort == 3;
        this.highTypeSk.node.active = isSpecial;
        if (isSpecial){
            this.highTypeSk.clearTracks();
            this.highTypeSk.setAnimation(0, "idle", false);
        }
    }
}

export class BbwzBullPokerTypeView extends BbwzPokerTypeView{
    private typeSp: cc.Sprite;
    private multSp: cc.Sprite;
    private highTypeSk: sp.Skeleton;

    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.typeSp = this.getComponent("layout/typeSp", cc.Sprite);
        this.multSp = this.getComponent("layout/multSp", cc.Sprite);
        this.highTypeSk = this.getComponent("highSk", sp.Skeleton);
    }

    public setPokerTypeStyle(nType: number, nMult: number){
        this.typeSp.node.active = true;
        let atlasPath = BbwzPathHelper.gameTexturePath + "atlas/poker/atlas_poker";
        Global.ResourceManager.loadBundleAutoAtlas(BbwzConstDefine.GAME_ID,this.typeSp, atlasPath, BbwzPokerTool.bullPokerTypeSf[nType], null, true);
        let sort = BbwzPokerTool.sortBullPokerType(nType);
        if (sort == 3){
            this.multSp.node.active = true;
            this.highTypeSk.node.active = true;
            this.highTypeSk.clearTracks();
            this.highTypeSk.setAnimation(0, "idle", false);
            Global.ResourceManager.loadBundleAutoAtlas(BbwzConstDefine.GAME_ID,this.multSp, atlasPath, BbwzPokerTool.multiSfRes[nMult], null, true);
        }
        else{
            this.highTypeSk.node.active = false;
            this.multSp.node.active = true;
            Global.ResourceManager.loadBundleAutoAtlas(BbwzConstDefine.GAME_ID,this.multSp, atlasPath, BbwzPokerTool.multiSfRes[nMult], null, true);
        }
    }
}